import Swiper from 'swiper';

$(function() {
    if($(window).width() < 576) {

        $('.portfolio__example--1, .portfolio__example--2').wrapAll('<div class="portfolio__slide swiper-slide">')
        $('.portfolio__example--3, .portfolio__example--4').wrapAll('<div class="portfolio__slide swiper-slide">')
        $('.portfolio__example--5, .portfolio__example--6').wrapAll('<div class="portfolio__slide swiper-slide">')
        $('.portfolio__btn').wrapAll('<div class="portfolio__slide swiper-slide">')

        var portfolio = new Swiper('.portfolio', {
            slidesPerView: 'auto',
            keyboard: {
                enabled: true,
            },
            spaceBetween: 20,
        });
    }
})