import Swiper from 'swiper';

$(function() {
    var formSlider = new Swiper('.slide-form__container', {
        simulateTouch: false,
        followFinger: false,
    });

    $('.slide-form__control-btn').click(function() {
        $('.slide-form__control-btn--active').removeClass('slide-form__control-btn--active');
        $(this).addClass('slide-form__control-btn--active')
    })
    $('.slide-form__control-btn--1').click(function() {
        formSlider.slideTo(0)
    })
    $('.slide-form__control-btn--2').click(function() {
        formSlider.slideTo(1)
    })
    $('.slide-form__control-btn--3').click(function() {
        formSlider.slideTo(2)
    })
})